// ShadowRenderTexture.js
var ShadowRenderTexture = pc.createScript('shadowRenderTexture');

ShadowRenderTexture.attributes.add('image', { type: 'entity' })
ShadowRenderTexture.attributes.add('model', { type: 'entity' })

// initialize code called once per entity
ShadowRenderTexture.prototype.initialize = function () {
    // console.log("camera", this.app.graphicsDevice)

    var colorBuffer = new pc.Texture(this.app.graphicsDevice, {
        width: 1024,
        height: 1024,
        format: pc.PIXELFORMAT_R8_G8_B8
    });
    var renderTarget = new pc.RenderTarget({
        colorBuffer: colorBuffer,
        depth: true
    });
    console.log(this.model)
    var material = this.model.render.meshInstances[0].material;
    material.diffuseMap = renderTarget.colorBuffer;
    material.update();
    // this.image.c.element.texture = renderTarget.colorBuffer//this.app.assets.get(107691829).resource
    //renderTarget.colorBuffer
    console.log(this.image);

};

// update code called every frame
ShadowRenderTexture.prototype.update = function (dt) {

};

