// UIScaling.js
var Uiscaling = pc.createScript('uiscaling');
Uiscaling.attributes.add('landScapeScaleMultiplier', { type: 'number', default: 1 })
Uiscaling.attributes.add('portraitScaleMultiplier', { type: 'number', default: 1 })

// initialize code called once per entity
Uiscaling.prototype.initialize = function () {
    this.baseScale = new pc.Vec3().copy(this.entity.getLocalScale());

    this.app.graphicsDevice.on('resizecanvas', (width, height) => {
        this.resize(width, height)
    });
    this.resize(this.app.graphicsDevice._width * 2, this.app.graphicsDevice._height * 2)





};

// update code called every frame
Uiscaling.prototype.resize = function (width, height) {

    let ratio = (width / 2) / (height / 2);
    let constant = 0.5625;
    let scale = 1 * ratio / constant;
    if (ratio > 1) {
        scale = this.landScapeScaleMultiplier * 1 / constant;
    }
    else {
        scale = this.portraitScaleMultiplier * 1 / constant;
    }
    this.entity.setLocalScale(this.baseScale.x * scale, this.baseScale.y * scale, scale)
};

