// SetToScreenSize.js
var SetToscreenSize = pc.createScript('setToscreenSize');

// initialize code called once per entity
SetToscreenSize.prototype.initialize = function () {
    this.shadowCamera = GameManager.instance.camera.findByName("Shadow Camera");
    this.sausageCamera = GameManager.instance.camera.findByName("Sausage Camera");

    setTimeout(() => {
        this.updateScreens(this.app.graphicsDevice._width * 2, this.app.graphicsDevice._height * 2)

    }, 100);
    this.app.graphicsDevice.on('resizecanvas', (width, height) => {
        this.updateScreens(width, height)
    });


};

SetToscreenSize.prototype.updateScreens = function (width, height) {
    this.entity.screen.referenceResolution.set(width / 2, height / 2);
    this.shadowCamera.script.renderCameraToelement.renderResolution = new pc.Vec2(width / 2, height / 2);
    this.shadowCamera.script.renderCameraToelement.createNewRenderTexture();
    this.sausageCamera.script.renderCameraToelement.renderResolution = new pc.Vec2(width / 2, height / 2);
    this.sausageCamera.script.renderCameraToelement.createNewRenderTexture();
};


// update code called every frame
SetToscreenSize.prototype.update = function (dt) {

};

