// transitionScreen.js
var TransitionScreen = pc.createScript('transitionScreen');
TransitionScreen.attributes.add('sausage', { type: 'entity' });
TransitionScreen.attributes.add('newSkin', { type: 'entity' });
TransitionScreen.attributes.add('shine', { type: 'entity' });
TransitionScreen.attributes.add('toTheme', { type: 'entity', array: true });
TransitionScreen.attributes.add('levelTexts', { type: 'entity', array: true });

// initialize code called once per entity
TransitionScreen.prototype.initialize = function () {

    this.levelTexts[0].element.text = GameManager.instance.levelId - 1;
    this.levelTexts[1].element.text = GameManager.instance.levelId;
    this.levelTexts[2].element.text = GameManager.instance.levelId + 1;
    this.toTheme.forEach((entity) => {
        entity.sprite.color = new pc.Color().fromString(ThemeManager.instance.theme.backgroundColor);
    });

    this.levelTexts.forEach((entity) => {
        entity.element.color = new pc.Color().fromString(ThemeManager.instance.theme.backgroundColor);
    });


    this.entity.findByName('Background').sprite.color = new pc.Color().fromString(ThemeManager.instance.theme.backgroundElementsColor);

    if (Object.values(GameManager.instance.skinList.resources[0]).find(skin => skin.level == (GameManager.instance.levelId + 1))) {
        this.newSkin.enabled = true;
    }


    this.entity.setLocalScale(0, 0, 0)


    this.entity
        .tween(this.entity.getLocalScale())
        .to(new pc.Vec3(1.25, 1.25, 1.25), .5, pc.ElasticOut)
        .on('complete', () => {
            this.sausage
                .tween(this.sausage.getLocalPosition())
                .to(new pc.Vec3(-0.35, this.sausage.getLocalPosition().y, this.sausage.getLocalPosition().z), .5, pc.Linear)
                .on('complete', () => {
                    this.shine
                        .tween(this.shine.getLocalScale())
                        .to(new pc.Vec3(1, 1, 1), 2, pc.ElasticOut)
                        .start();

                    setTimeout(() => {
                        this.entity
                            .tween(this.entity.getLocalScale())
                            .to(new pc.Vec3(0, 0, 0), .5, pc.ElasticIn)
                            .on('complete', () => {
                                GameManager.instance.startLevel()
                                this.entity.parent.destroy()
                            })
                            .start()
                    }, 500);
                })
                .start();
        })
        .start();


};

// update code called every frame
TransitionScreen.prototype.update = function (dt) {

};

