// boxeGlove.js
var BoxeGlove = pc.createScript('boxeGlove');

BoxeGlove.attributes.add('force', { type: 'number', default: 30 });
BoxeGlove.attributes.add('collide', { type: 'entity' });

// initialize code called once per entity
BoxeGlove.prototype.initialize = function () {
    this.waitingLaunch = false;
    this.rb = this.entity.rigidbody;
    this.collide.collision.on('triggerenter', (event) => {
        if (this.waitingLaunch || !event.tags._list.includes("Segment")) return;
        this.launch();

    }, this);


};

BoxeGlove.prototype.launch = function () {

    let rotation = this.entity.getRotation().getEulerAngles();
    let force = new pc.Vec3().copy(this.entity.up).mulScalar((this.force * 2));
    let force2 = new pc.Vec3().copy(this.entity.up).mulScalar((this.force));
    force2.y = -force2.y;
    GameManager.instance.playerController.dash(force2);
    GameManager.instance.playerController.unstick();

    this.rb.applyImpulse(force.x, force.y, force.z);

    this.waitingLaunch = true;
    setTimeout(() => {
        this.waitingLaunch = false;
    }, 500);
};
// update code called every frame
BoxeGlove.prototype.update = function (dt) {

};

