// onBoarding.js
var OnBoarding = pc.createScript('onBoarding');
OnBoarding.attributes.add('handPosition', {
    type: 'vec2',
    default: [0, 0]
});
OnBoarding.attributes.add('sausagePosition', {
    type: 'vec2',
    default: [0, 0]
});
OnBoarding.attributes.add('leftKey', {
    type: 'entity'
});
OnBoarding.attributes.add('rightKey', {
    type: 'entity'
});
OnBoarding.attributes.add('controlText', {
    type: 'entity'
});
OnBoarding.attributes.add('block', {
    type: 'entity'
});

// initialize code called once per entity
OnBoarding.prototype.initialize = function () {
    this.sausage = this.entity.findByName("Sausage");
    this.dash = this.entity.findByName("Dash");
    this.hand = this.entity.findByName("Hand");

    this.hand.startPos = this.hand.getPosition();
    this.sausage.startPos = this.sausage.getPosition();
    this.dash.startScale = this.dash.getLocalScale();
    this.rightKey.startScale = this.rightKey.getLocalScale();
    this.leftKey.startScale = this.leftKey.getLocalScale();

    if (this.app.touch) {
        this.startMouseAnimation();
    }
    else {
        this.startKeyboardAnimation();
    }
};

OnBoarding.prototype.startMouseAnimation = function (dt) {
    if (this.controlText.element) {
        if (this.app.touch) {
            this.controlText.enabled = false;
        }
        else {
            this.controlText.element.text = "Mouse\nControls";
        }
        this.leftKey.enabled = false;
        this.rightKey.enabled = false;
        this.hand.enabled = true;
        if (this.hand.sprite) {
            this.dash.enabled = true;
            this.block.enabled = true;
            this.sausage.enabled = true;
            this.dash.setLocalScale(0, this.dash.startScale.y, this.dash.startScale.z);
            this.sausage.setPosition(this.sausage.startPos.x, this.sausage.startPos.y, this.sausage.startPos.z);
            this.sausage.setLocalEulerAngles(0, 0, 0);
            this.hand.setPosition(this.hand.startPos.x, this.hand.startPos.y, this.hand.startPos.z);
            this.hand.sprite.opacity = 1;

            this.dash
                .tween(this.dash.getLocalScale())
                .to(new pc.Vec3(0.619, this.dash.startScale.y, 0), 1.5, pc.SineOut)
                .on('complete', () => {
                    setTimeout(() => {
                        this.dash.enabled = false;
                        this.sausage
                            .tween(this.sausage.getLocalPosition())
                            .to(new pc.Vec3(this.sausagePosition.x, this.sausagePosition.y, 0), .6, pc.SineOut)
                            .on('complete', () => {
                                setTimeout(() => {
                                    this.leftKey.enabled = false;
                                    this.rightKey.enabled = false;
                                    this.block.enabled = false;
                                    this.sausage.enabled = false;
                                    this.hand.enabled = false;
                                    this.dash.enabled = false;
                                    if (this.app.touch) {
                                        this.startMouseAnimation()
                                    }
                                    else {
                                        this.startKeyboardAnimation()
                                    }
                                }, 1000);

                            })
                            .on('update', () => {
                                if (this.sausage)
                                    this.sausage.setLocalEulerAngles(0, 0, this.sausage.getLocalEulerAngles().z - 10);
                            })
                            .start();

                        let alpha = { x: 1 };
                        let tween = this.hand.tween(alpha);
                        tween.to({ x: 0 }, .6, pc.Linear)
                            .on('update', () => {
                                if (this.hand.sprite)
                                    this.hand.sprite.opacity = alpha.x;
                            })
                            .start();

                        this.hand
                            .tween(this.hand.getLocalPosition())
                            .to(new pc.Vec3(this.handPosition.x, this.handPosition.y - .5, 0), .6, pc.SineOut)
                            .on('update', () => {
                            })
                            //   .loop(true)
                            .start();


                    }, 250);
                })
                //  .loop(true)
                .start();

            this.hand
                .tween(this.hand.getLocalPosition())
                .to(new pc.Vec3(this.handPosition.x, this.handPosition.y, 0), 1.2, pc.SineOut)
                .on('update', () => {
                })
                //   .loop(true)
                .start();

        }


    }


};

OnBoarding.prototype.startKeyboardAnimation = function (dt) {
    if (this.controlText.element) {

        this.controlText.element.text = "KEYBOARD\nControls";
        this.leftKey.enabled = true;
        this.rightKey.enabled = true;
        this.block.enabled = true;
        this.sausage.enabled = true;
        this.hand.enabled = false;
        this.dash.enabled = true;
        this.sausage.setPosition(this.sausage.startPos.x, this.sausage.startPos.y, this.sausage.startPos.z);
        this.sausage.setLocalEulerAngles(0, 0, 0);

        this.dash.setLocalEulerAngles(new pc.Vec3(0, 0, -23.25))
        this.leftKey
            .tween(this.leftKey.getLocalScale())
            .to(new pc.Vec3(this.leftKey.startScale.y / 2, this.leftKey.startScale.y / 2, 0), 1, pc.SineOut)
            .on('loop', () => {
                this.dash.enabled = false;
                this.sausage
                    .tween(this.sausage.getLocalPosition())
                    .to(new pc.Vec3(this.sausagePosition.x, this.sausagePosition.y, 0), .6, pc.SineOut)
                    .on('complete', () => {
                        setTimeout(() => {
                            /*       this.leftKey.enabled = false;
                                  this.rightKey.enabled = false;
                                  this.block.enabled = false;
                                  this.sausage.enabled = false;
                                  this.hand.enabled = false;
                                  this.dash.enabled = false; */
                            this.startMouseAnimation();
                        }, 1000);
                    })
                    .on('update', () => {

                        if (this.sausage)
                            this.sausage.setLocalEulerAngles(0, 0, this.sausage.getLocalEulerAngles().z - 10);
                    })
                    .start();

                console.log("tete")
            })
            .on('update', () => {
            })
            .repeat(2)
            .yoyo(true)
            .start()

        this.dash
            .tween(this.dash.getLocalEulerAngles())
            .rotate(new pc.Vec3(0, 0, 23.25), 1, pc.Linear)
            .start();
    }

}
// update code called every frame
OnBoarding.prototype.update = function (dt) {

};

