// ShopManager.js
var ShopManager = pc.createScript('shopManager');
ShopManager.attributes.add('skinData', {
    type: 'asset',
    assetType: 'json'
});
ShopManager.attributes.add('blistersLevel', {
    type: 'entity',
});
ShopManager.attributes.add('blistersRewarded', {
    type: 'entity',
});
ShopManager.attributes.add('pages', {
    type: 'entity',
    array: true
});
ShopManager.attributes.add('pageNext', {
    type: 'entity'
});
ShopManager.attributes.add('pagePrevious', {
    type: 'entity'
});
ShopManager.attributes.add('immovable', {
    type: 'entity'
});
ShopManager.attributes.add('skinSpawnPoint', {
    type: 'entity'
});
ShopManager.attributes.add('numberSkins', {
    type: 'entity',
});
ShopManager.attributes.add('slideLimit', {
    type: 'number'
});
ShopManager.attributes.add('maxHeight', {
    type: 'number'
});
ShopManager.attributes.add('minHeight', {
    type: 'number'
});

// initialize code called once per entity
ShopManager.prototype.initialize = function () {
    ShopManager.instance = this;
    this.app.timeScale = 1;
    this.selectedPage = this.pages[1];
    this.populateShop();

    this.pageNext.button.on('click', (event) => {
        this.nextPage()
    });
    this.pagePrevious.button.on('click', (event) => {
        this.previousPage()
    });

    var touch = this.app.touch;
    if (touch) {
        touch.on(pc.EVENT_TOUCHSTART, this.onMouseDown, this);
        touch.on(pc.EVENT_TOUCHMOVE, this.onMouseMove, this);
        touch.on(pc.EVENT_TOUCHEND, this.onMouseUp, this);

    }
    else {
        this.app.mouse.on(pc.EVENT_MOUSEMOVE, this.onMouseMove, this);
        this.app.mouse.on(pc.EVENT_MOUSEDOWN, this.onMouseDown, this);
        this.app.mouse.on(pc.EVENT_MOUSEUP, this.onMouseUp, this);
    }



    this.on('destroy', function () {
        if (this.app.mouse) {
            this.app.mouse.off(pc.EVENT_MOUSEDOWN, this.onMouseDown, this);
            this.app.mouse.off(pc.EVENT_MOUSEUP, this.onMouseUp, this);
            this.app.mouse.off(pc.EVENT_MOUSEMOVE, this.onMouseMove, this);
        }

        if (this.app.touch) {
            this.app.touch.off(pc.EVENT_TOUCHSTART, this.onMouseDown, this);
            this.app.touch.off(pc.EVENT_TOUCHEND, this.onMouseUp, this);
            this.app.touch.off(pc.EVENT_TOUCHMOVE, this.onMouseMove, this);
        }
    }, this);

    this.shopOffsetY = 0;
    this.lastShopOffsetY = 0;
    this.yVelocity = 0
    this._time = pc.now();
    this.immovable.setPosition(CameraManager.instance.entity.getPosition().x, CameraManager.instance.entity.getPosition().y - 5, this.immovable.getPosition().z)

};

ShopManager.prototype.nextPage = function () {
    if (this.pages[this.pages.indexOf(this.selectedPage) + 1]) {
        this.selectedPage = this.pages[this.pages.indexOf(this.selectedPage) + 1]
        CameraManager.instance.shopOffsetY = 0;
        this.shopOffsetY = CameraManager.instance.shopOffsetY;
    }
}

ShopManager.prototype.previousPage = function () {
    if (this.pages[this.pages.indexOf(this.selectedPage) - 1]) {
        this.selectedPage = this.pages[this.pages.indexOf(this.selectedPage) - 1]
        CameraManager.instance.shopOffsetY = 0;
        this.shopOffsetY = CameraManager.instance.shopOffsetY;
    }
}
ShopManager.prototype.populateShop = function () {
    let blister;

    Object.values(this.skinData.resources[0]).forEach((skin, index) => {
        if (skin.type == "BASIC") {
            if (this.blistersLevel.children[skin.order - 1] == null) return;
            blister = this.blistersLevel.children[skin.order - 1].script.skinBlister;
            blister.setBasic()
            blister.levelText.element.text = ("LV." + skin.level.toString());
            blister.levelText.parent.setRotation(new pc.Quat().setFromEulerAngles(0, 0, randomNumber(-35, 35)));


        }
        else if (skin.type == "REWARDED") {
            if (this.blistersRewarded.children[skin.order - 1] == null) return;
            blister = this.blistersRewarded.children[skin.order - 1].script.skinBlister;
            blister.setRewarded()
        }
        blister.skin = skin.name;
        if (SaveManager.instance.getItem("Skins").includes(skin.name)) {
            blister.unlock()
        }
        blister.setSelected(false)
        if (SaveManager.instance.getItem("CurrentSkin") == skin.name) {
            blister.setSelected(true)
            blister.selected = true;
            this.currentSkin = blister
        }



        blister.toColorize.forEach((toColorize) => {
            toColorize.sprite.color = new pc.Color().fromString(skin.packagingColor);
        });


        let asset;
        if (this.app.assets.find(skin.name, "template")) {
            asset = this.app.assets.find(skin.name, "template").resource.instantiate();
            doStuffToEntity(this.app.assets.find(skin.name, "template").resource._templateRoot, asset)
        }
        else {
            asset = this.app.assets.find("Basic", "template").resource.instantiate();
            doStuffToEntity(this.app.assets.find("Basic", "template").resource._templateRoot, asset)
        }

        asset.script.playerController.enabled = false;
        asset.script.skinDisplayer.enabled = true;

        this.numberSkins.element.text = SaveManager.instance.getItem('Skins').length.toString() + " / 15"
        console.log(SaveManager.instance.getItem('Skins').length)

        let position = blister.skinSpawnPoint.getPosition();
        asset.setPosition(position.x, position.y, position.z)
        this.entity.parent.addChild(asset);



    });
};


ShopManager.prototype.unlockSkin = function (blister) {

    let skinList = SaveManager.instance.getItem("Skins")
    skinList.push(blister.skin)
    SaveManager.instance.setItem("Skins", skinList);

    blister.unlock();
    ShopManager.instance.selectedSkin.setSelected(false);
    blister.setSelected(true)

};
ShopManager.prototype.displaySkin = function (blister) {
    if (this.displayedSkin) {
        this.displayedSkin.destroy()
    }
    this.displayedSkin = this.app.assets.find(this.selectedSkin.skin, "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find(this.selectedSkin.skin, "template").resource._templateRoot, this.displayedSkin);
    this.displayedSkin.script.playerController.enabled = false;
    this.displayedSkin.script.skinDisplayer.enabled = true;
    let position = this.skinSpawnPoint.getPosition();
    this.displayedSkin.setPosition(position.x, position.y, position.z)
    this.entity.parent.addChild(this.displayedSkin);

}
// update code called every frame
ShopManager.prototype.postUpdate = function (dt) {
    if (CameraManager.instance && this.prev) {

        this.yVelocity = CameraManager.instance.getPosition().y - this.prev / (dt)
        this.prev = CameraManager.instance.getPosition().y;
    }
    //this.ui.setPosition(CameraManager.instance.entity.getPosition().x, CameraManager.instance.entity.getPosition().y, this.ui.getPosition().z)
    this.immovable.setPosition(this.immovable.getPosition().x, CameraManager.instance.entity.getPosition().y - 5, this.immovable.getPosition().z)
};


ShopManager.prototype.onMouseDown = function (event) {
    let x, y;

    if (event.hasOwnProperty("touches")) {

        x = event.touches[0].touch.clientX;
        y = event.touches[0].touch.clientY;

    }
    else {
        x = event.x;
        y = event.y;
    }

    this.mouseDown = true;
    this.startPos = new pc.Vec2(x, y)
    this.prev = 0;

    this.axis = 2


    event.event.preventDefault();

};

ShopManager.prototype.onMouseMove = function (event) {
    let x, y;
    if (event.hasOwnProperty("touches")) {

        x = event.touches[0].touch.clientX;
        y = event.touches[0].touch.clientY;
    }
    else {
        x = event.x;
        y = event.y;
    }

    if (this.mouseDown) {
        this.mousePos = new pc.Vec2(x, y);
        this.delta = this.mousePos.sub(this.startPos).mulScalar(GameManager.instance.camera.camera.orthoHeight * 2 / window.innerHeight);
        if ((this.delta.x < -1 || this.delta.x > 1) && this.axis == 2) {
            this.axis = 0;
        }
        else if ((this.delta.y < -1 || this.delta.y > 1) && this.axis == 2) {
            this.axis = 1;
        }
        if (this.axis == 0) {
            CameraManager.instance.shopOffsetX = -this.delta.x;
        }
        else {

            CameraManager.instance.shopOffsetY = clamp(this.shopOffsetY + this.delta.y, (this.pages.indexOf(this.selectedPage) == 1) ? -27 : -5, 5);

        }
    }

    event.event.preventDefault();

};

ShopManager.prototype.onMouseUp = function (event) {
    if (this.axis == 1) {
        let offset = (this.pages.indexOf(this.selectedPage) == 1) ? 22 : 0;

        if (CameraManager.instance.shopOffsetY > 0) {
            this.shopOffsetY = 0;

        }
        else if (CameraManager.instance.shopOffsetY < -offset) {
            this.shopOffsetY = -offset;
        }
        else {
            this.shopOffsetY = CameraManager.instance.shopOffsetY;

        }

        CameraManager.instance.shopOffsetY = this.shopOffsetY;
    }
    else {
        if (this.delta) {
            if (this.delta.x > this.slideLimit) {
                this.nextPage()
            }
            else if (this.delta.x < -this.slideLimit) {
                this.previousPage()
            }
        }


    }
    CameraManager.instance.shopOffsetX = 0;
    this.mouseDown = false;
    this.axis = 3
    event.event.preventDefault();


};


