PokiManager.prototype.rewarded = function (onComplete, rewardedData) {
    if (!this.rewardedOn) {
        PokiSDK.gameplayStop();
        this.rewardedOn = true;
        PokiSDK.rewardedBreak(rewardedData.category, rewardedData.details, rewardedData.placement)
            .then((withReward) => {
                this.rewardedOn = false;
                if (!withReward) {
                    this.app.fire('shopAdblockPopup');
                }
                onComplete(withReward)

            })
            .catch((err) => {
                this.rewardedOn = false;
                console.log("error")
            })
    }

    else {
        onComplete(false)
    }
}

