// springRenderer.js
var SpringRenderer = pc.createScript('springRenderer');
SpringRenderer.attributes.add('springEntity', { type: 'entity' });

// initialize code called once per entity
SpringRenderer.prototype.initialize = function () {

    if (this.springEntity != null) {
        this.springJoint = this.springEntity.script.springJoint;
        if (this.springJoint == null) {
            this.springJoint = this.springEntity.script.spring;
        }
    }
    else {
        this.springJoint = this.entity.script.springJoint;
        if (this.springJoint == null) {
            this.springJoint = this.entity.script.spring;
        }
    }
    this.spriteRenderer = this.entity.children[0].sprite;

    if (this.springJoint == null) return;

    this.scaleX = this.entity.getLocalScale().x;
    this.scaleY = this.entity.getLocalScale().y;

    this.width = this.entity.children[0].sprite.width;

    this.connectedTranform = this.springJoint.entityB;



    /*     this.size = new pc.Vec2(this.spriteRenderer.width, this.spriteRenderer.height);
        this.size.x = this.entity.getPosition().sub(this.connectedTranform.getPosition()).y
        this.spriteRenderer.width = Math.abs(this.size.x);
        this.spriteRenderer.height = this.size.y; */

};

SpringRenderer.prototype.updateSprite = function () {

    if (this.connectedTranform == null || this.spriteRenderer == null) return;
    let position = new pc.Vec3(0, (this.entity.getLocalPosition().y + this.connectedTranform.getLocalPosition().y) / 2, 0);
    // this.spriteRenderer.entity.setLocalPosition(position)
    this.size = new pc.Vec2(this.spriteRenderer.width, this.spriteRenderer.height);
    this.size.x = this.entity.getPosition().sub(this.connectedTranform.getPosition())
    this.entity.setLocalScale(this.scaleX, (Math.abs(this.entity.getPosition().sub(this.connectedTranform.getPosition()).length()) / this.width) / this.entity.parent.getScale().y, 1)
    this.spriteRenderer.height = this.size.y;

    let delta = this.entity.getPosition().sub(this.connectedTranform.getPosition());

    this.entity.setRotation(new pc.Quat().setFromEulerAngles(new pc.Vec3(0, 0, Math.atan2(delta.y, delta.x) * (180 / Math.PI) + 90)));

};
// update code called every frame
SpringRenderer.prototype.update = function (dt) {
    this.updateSprite()
};

