// newSkinPopup.js
var NewSkinPopup = pc.createScript('newSkinPopup');
NewSkinPopup.attributes.add('skinSpawnPoint', {
    type: 'entity'
});
NewSkinPopup.attributes.add('equipButton', {
    type: 'entity'
});
NewSkinPopup.attributes.add('quitButton', {
    type: 'entity'
});
NewSkinPopup.attributes.add('rewardedIcon', {
    type: 'entity'
});
NewSkinPopup.attributes.add('newSkinText', {
    type: 'entity'
});
NewSkinPopup.attributes.add('shine', {
    type: 'entity'
});
// initialize code called once per entity
NewSkinPopup.prototype.initialize = function () {

    this.equipButton.button.on('click', () => {
        if (this.rewarded) {
            PokiManager.instance.rewarded((success) => {
                if (success) {
                    let skinList = SaveManager.instance.getItem("Skins")
                    skinList.push(this.skin)
                    SaveManager.instance.setItem("Skins", skinList);
                    SaveManager.instance.setItem("CurrentSkin", this.skin);
                    GameManager.instance.startLevel();

                }

                SkinsButton.instance.showButton();
                GameManager.instance.playerController.activated = true;
                this.entity.destroy()
            }, { category: "cosmetic", detail: this.skin, placement: "popup" });
        }
        else {
            SaveManager.instance.setItem("CurrentSkin", this.skin);
            GameManager.instance.startLevel();
            this.entity.destroy();
        }

    });
    this.quitButton.button.on('click', () => {
        SkinsButton.instance.showButton();
        GameManager.instance.playerController.activated = true;
        this.entity.destroy()
    });
    this.displayedSkin = this.app.assets.find(this.skin, "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find(this.skin, "template").resource._templateRoot, this.displayedSkin);
    this.displayedSkin.script.playerController.enabled = false;
    this.displayedSkin.script.skinDisplayer.enabled = true;
    let position = this.skinSpawnPoint.getPosition();
    this.displayedSkin.setPosition(position.x, position.y, position.z)
    this.entity.addChild(this.displayedSkin);
    if (this.rewarded) {
        this.equipButton.findByName("Text").element.text = "UNLOCK"
        this.equipButton.element.color = new pc.Color().fromString("F8B100")
        this.rewardedIcon.enabled = true;
        this.newSkinText.enabled = false;
        this.shine.enabled = false;
    }
    //

};

// update code called every frame
NewSkinPopup.prototype.update = function (dt) {

};

