// compoundConstraint.js
var CompoundConstraint = pc.createScript('compoundConstraint');

// initialize code called once per entity
CompoundConstraint.prototype.initialize = function () {
    setTimeout(() => {

        this.entity.parent.script.hingeConstraint.createConstraint();
        this.entity.parent.script.hingeConstraint.enabled = true;
    }, 1);
    this.on('enable', function () {

        this.entity.parent.script.hingeConstraint.createConstraint(); this.entity.parent.script.hingeConstraint.enabled = true;

    });
};

// update code called every frame
CompoundConstraint.prototype.update = function (dt) {

};

