// pointerLock.js
var PointerLock = pc.createScript('pointerLock');
PointerLock.attributes.add('pointer', { type: 'entity' });
PointerLock.attributes.add('sides', { type: 'entity', array: true });

// initialize code called once per entity
PointerLock.prototype.initialize = function () {
    PointerLock.instance = this;
    this.locked = false;
    this.init = false;
    this.app.mouse.disableContextMenu();

    document.body.style.cursor = 'none';
    document.addEventListener("mouseleave", (event) => {

        if (event.clientX <= 0) {
            this.blink(this.sides[3])
        }
        if (event.clientX >= window.innerWidth) {
            this.blink(this.sides[1])
        }
        if (event.clientY <= 0) {
            this.blink(this.sides[0])
        }
        if (event.clientY >= window.innerWidth) {
            this.blink(this.sides[2])

        }

    });

    this.app.mouse.on(pc.EVENT_MOUSEMOVE, this.onMouseMove, this);
    /*   this.app.keyboard.on(pc.EVENT_KEYDOWN, () => {
  
          if (pc.Mouse.isPointerLocked()) {
              this.disablePointerLock()
          }
          else {
              this.enablePointerLock()
          }
      }, this);
  
      this.app.mouse.on(pc.EVENT_MOUSEDOWN, this.onMouseDown, this);
      this.app.mouse.on(pc.EVENT_MOUSEMOVE, this.onMouseMove, this); */



};

PointerLock.prototype.blink = function (side) {
    let alpha = { x: 0 };

    let tween = side.tween(alpha);
    tween.to({ x: .5 }, .25, pc.Linear, 0, 1)
        .on('update', () => {
            tween.manager.setTs(1 / this.app.timeScale)
            side.element.opacity = alpha.x;
        })
        .yoyo(true)
        .repeat(2)
        .start();
};
PointerLock.prototype.enablePointerLock = function (event) {

    this.app.mouse.enablePointerLock();
    this.app.mouse.disableContextMenu();
    this.locked = true;

}
PointerLock.prototype.disablePointerLock = function (event) {
    this.app.mouse.disablePointerLock();
    this.locked = false;

}

PointerLock.prototype.onMouseDown = function (event) {
    /*    if (!pc.Mouse.isPointerLocked() && PlayerController.instance && PlayerController.instance.activated) {
           this.enablePointerLock()
           this.x = event.x
           this.y = event.y
       } */

};
PointerLock.prototype.onMouseMove = function (event) {

    if (!this.init) {
        this.init = true
        this.x = event.x
        this.y = event.y
    }
    if (pc.Mouse.isPointerLocked()) {
        this.x += event.event.movementX;
        this.y += event.event.movementY;

        if (this.x < 0) {
            this.x = 0;
        }
        if (this.y < 0) {
            this.y = 0;
        }
    }

    else {

    }
    this.x = event.x;
    this.y = event.y;
    this.point = CameraManager.instance.entity.camera.screenToWorld(this.x, this.y, 0);
    this.pointer.setPosition(this.point.x, this.point.y, 0)


};
// update code called every frame
PointerLock.prototype.update = function (dt) {

};

