// LoadSceneOnButtonClick.js
var LoadSceneOnButtonClick = pc.createScript('loadSceneOnButtonClick');
LoadSceneOnButtonClick.attributes.add('sceneName', { type: 'string' });


// initialize code called once per entity
LoadSceneOnButtonClick.prototype.initialize = function () {
    this.clicked = false;
    this.entity.button.on('click', function () {

        /*         if (!this.clicked) {
                    this.clicked = true; */
        if (this.sceneName == "Shop") {
            GameManager.instance.shopScene()
        }
        else if (this.sceneName == "Game") {
            GameManager.instance.startLevel()

        }
        else if (this.sceneName == "Menu") {
            GameManager.instance.mainMenu()

        }
        else if (this.sceneName == "Return") {
            if (GameManager.instance.lastScreen.name == "Main Menu") {
                GameManager.instance.mainMenu()
            }
            else {
                GameManager.instance.startLevel()
            }

        }
        else if (this.sceneName == "Unlock") {
            GameManager.instance.unlockLevelSkin("Cosmonaut")

        }
        else if (this.sceneName == "Skip") {
            PokiManager.instance.rewarded((success) => {
                if (success) {
                    GameManager.instance.levelId++;
                    GameManager.instance.levelStartFlag = true;
                }

            }, { category: "level-skip", detail: GameManager.instance.levelId.toString(), placement: "gameplay" });


        }
        // }
    }, this);
};

LoadSceneOnButtonClick.prototype.ad = async function (nb) {


};


