// Shadow.js
var Shadow = pc.createScript('shadow');

Shadow.attributes.add('offset', {
    title: 'Offset',
    type: 'vec3',
    default: [0.25, -0.25, 0]
});

Shadow.attributes.add('color', {
    title: 'Color',
    type: 'rgb',
    default: [0, 0, 0]
});

// initialize code called once per entity
Shadow.prototype.initialize = function () {

    let sprite = this.app.assets.get(this.entity.sprite.spriteAsset);
    this.shadow = new pc.Entity();
    this.shadow.addComponent('sprite', {
        type: pc.SPRITETYPE_SIMPLE,
        spriteAsset: sprite
    });
    this.shadow.name = this.entity.name + " Shadow";
    this.shadow.setPosition(this.entity.getPosition());
    this.shadow.setRotation(this.entity.getRotation());
    this.shadow.setLocalScale(this.entity.getLocalScale());
    this.shadow.sprite.width = this.entity.sprite.width;
    this.shadow.sprite.height = this.entity.sprite.height;
    this.shadow.sprite.color = this.color;
    this.shadow.sprite.layers = [1004];
    this.shadow.sprite.opacity = 0.25;
    var material = this.shadow.sprite._material.clone();
    material.blendType = pc.BLEND_SUBTRACTIVE
    material.blendType.alphaWrite = false;

    material.update();
    this.shadow.sprite._meshInstance.material = material;
    this.app.root.addChild(this.shadow);
};

// update code called every frame
Shadow.prototype.update = function (dt) {
    this.shadow.setRotation(this.entity.getRotation());
    this.shadow.setPosition(new pc.Vec3().copy(this.entity.getPosition()).add(this.offset))
};

