// height-to-normal.js
var HeightToNormal = pc.createScript('heightToNormal');

HeightToNormal.attributes.add('blurIterations', { type: 'number', default: 2 });
HeightToNormal.attributes.add('strength', { type: 'number', min: 0, max: 1, default: 1 });

pc.GraphicsDevice.prototype.setUniform = function (name, value) {
    this.scope.resolve(name).setValue(value);
};

HeightToNormal.createShaders = function (app) {
    var postEffectVert = [
        "attribute vec2 aPosition;",
        "",
        "varying vec2 vUv0;",
        "",
        "void main()",
        "{",
        "    gl_Position = vec4(aPosition, 0.0, 1.0);",
        "    vUv0 = aPosition.xy * 0.5 + 0.5;",
        "}"    
    ].join("\n");

    var copyFrag = [
        "precision " + app.graphicsDevice.precision + " float;",
        "",
        "varying vec2 vUv0;",
        "",
        "uniform sampler2D uColorBuffer;",
        "",
        "void main()",
        "{",
        "    gl_FragColor = texture2D(uColorBuffer, vUv0);",
        "}"
    ].join("\n");

    var blurFrag = [
        "precision " + app.graphicsDevice.precision + " float;",
        "",
        "varying vec2 vUv0;",
        "",
        "uniform vec2 uResolution;",
        "uniform sampler2D uColorBuffer;",
        "uniform vec2 uDirection;",
        "",
        "vec4 blur(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {",
        "    vec4 color = vec4(0.0);",
        "    vec2 off1 = vec2(1.411764705882353) * direction;",
        "    vec2 off2 = vec2(3.2941176470588234) * direction;",
        "    vec2 off3 = vec2(5.176470588235294) * direction;",
        "    color += texture2D(image, uv) * 0.1964825501511404;",
        "    color += texture2D(image, uv + (off1 / resolution)) * 0.2969069646728344;",
        "    color += texture2D(image, uv - (off1 / resolution)) * 0.2969069646728344;",
        "    color += texture2D(image, uv + (off2 / resolution)) * 0.09447039785044732;",
        "    color += texture2D(image, uv - (off2 / resolution)) * 0.09447039785044732;",
        "    color += texture2D(image, uv + (off3 / resolution)) * 0.010381362401148057;",
        "    color += texture2D(image, uv - (off3 / resolution)) * 0.010381362401148057;",
        "    return color;",
        "}",
        "",
        "void main()",
        "{",
        "    gl_FragColor = blur(uColorBuffer, vUv0, uResolution, uDirection);",
        "}"
    ].join("\n");
    
    var normalMapFrag = [
        "precision " + app.graphicsDevice.precision + " float;",
        "",
        "varying vec2 vUv0;",
        "",
        "uniform vec2 uResolution;",
        "uniform float dz;",
        "uniform float invertR;",
        "uniform float invertG;",
        "uniform float invertH;",
        "uniform int type;",
        "uniform sampler2D uHeightMap;",
        "",
        "void main()",
        "{",
        "    vec2 step = vec2(-1.0 / uResolution.x, -1.0 / uResolution.y);",
        "    vec2 tlv = vec2(vUv0.x - step.x, vUv0.y + step.y );",
        "    vec2 lv  = vec2(vUv0.x - step.x, vUv0.y          );",
        "    vec2 blv = vec2(vUv0.x - step.x, vUv0.y - step.y);",
        "    vec2 tv  = vec2(vUv0.x         , vUv0.y + step.y);",
        "    vec2 bv  = vec2(vUv0.x         , vUv0.y - step.y);",
        "    vec2 trv = vec2(vUv0.x + step.x, vUv0.y + step.y );",
        "    vec2 rv  = vec2(vUv0.x + step.x, vUv0.y          );",
        "    vec2 brv = vec2(vUv0.x + step.x, vUv0.y - step.y);",
        "    tlv = vec2(tlv.x >= 0.0 ? tlv.x : (1.0 + tlv.x),  tlv.y >= 0.0	? tlv.y : (1.0  + tlv.y));",
        "    tlv = vec2(tlv.x < 1.0  ? tlv.x : (tlv.x - 1.0 ), tlv.y < 1.0  ? tlv.y : (tlv.y - 1.0 ));",
        "    lv  = vec2( lv.x >= 0.0 ?  lv.x : (1.0 + lv.x),   lv.y  >= 0.0 ?  lv.y : (1.0  +  lv.y));",
        "    lv  = vec2( lv.x < 1.0  ?  lv.x : ( lv.x - 1.0 ), lv.y  < 1.0  ?  lv.y : ( lv.y - 1.0 ));",
        "    blv = vec2(blv.x >= 0.0 ? blv.x : (1.0 + blv.x),  blv.y >= 0.0 ? blv.y : (1.0  + blv.y));",
        "    blv = vec2(blv.x < 1.0  ? blv.x : (blv.x - 1.0 ), blv.y < 1.0  ? blv.y : (blv.y - 1.0 ));",
        "    tv  = vec2( tv.x >= 0.0 ?  tv.x : (1.0 + tv.x),   tv.y  >= 0.0 ? tv.y  : (1.0  +  tv.y));",
        "    tv  = vec2( tv.x < 1.0  ?  tv.x : ( tv.x - 1.0 ), tv.y  < 1.0  ? tv.y  : ( tv.y - 1.0 ));",
        "    bv  = vec2( bv.x >= 0.0 ?  bv.x : (1.0 + bv.x),   bv.y  >= 0.0 ? bv.y  : (1.0  +  bv.y));",
        "    bv  = vec2( bv.x < 1.0  ?  bv.x : ( bv.x - 1.0 ), bv.y  < 1.0  ? bv.y  : ( bv.y - 1.0 ));",
        "    trv = vec2(trv.x >= 0.0 ? trv.x : (1.0 + trv.x),  trv.y >= 0.0 ? trv.y : (1.0  + trv.y));",
        "    trv = vec2(trv.x < 1.0  ? trv.x : (trv.x - 1.0 ), trv.y < 1.0  ? trv.y : (trv.y - 1.0 ));",
        "    rv  = vec2( rv.x >= 0.0 ?  rv.x : (1.0 + rv.x),   rv.y  >= 0.0 ? rv.y  : (1.0  +  rv.y));",
        "    rv  = vec2( rv.x < 1.0  ?  rv.x : ( rv.x - 1.0 ), rv.y  < 1.0  ? rv.y  : ( rv.y - 1.0 ));",
        "    brv = vec2(brv.x >= 0.0 ? brv.x : (1.0 + brv.x),  brv.y >= 0.0 ? brv.y : (1.0  + brv.y));",
        "    brv = vec2(brv.x < 1.0  ? brv.x : (brv.x - 1.0 ), brv.y < 1.0  ? brv.y : (brv.y - 1.0 ));",
        "    float tl = abs(texture2D(uHeightMap, tlv).r);",
        "    float l  = abs(texture2D(uHeightMap, lv ).r);",
        "    float bl = abs(texture2D(uHeightMap, blv).r);",
        "    float t  = abs(texture2D(uHeightMap, tv ).r);",
        "    float b  = abs(texture2D(uHeightMap, bv ).r);",
        "    float tr = abs(texture2D(uHeightMap, trv).r);",
        "    float r  = abs(texture2D(uHeightMap, rv ).r);",
        "    float br = abs(texture2D(uHeightMap, brv).r);",
        "    float dx = 0.0, dy = 0.0;",
        "    if (type == 0) // Sobel",
        "    {",
        "        dx = tl + l*2.0 + bl - tr - r*2.0 - br;",
        "        dy = tl + t*2.0 + tr - bl - b*2.0 - br;",
        "    }",
        "    else // Scharr",
        "    {",
        "        dx = tl*3.0 + l*10.0 + bl*3.0 - tr*3.0 - r*10.0 - br*3.0;",
        "        dy = tl*3.0 + t*10.0 + tr*3.0 - bl*3.0 - b*10.0 - br*3.0;",
        "    }",
        "    vec3 normal = normalize(vec3(dx * invertR * invertH * 255.0, dy * invertG * invertH * 255.0, dz));",
        "    gl_FragColor = vec4(normal.xyz * 0.5 + 0.5, 1.0);",
        "}"
    ].join("\n");
    
    var createShader = function (vs, fs) {
        var shaderDefinition = {
            attributes: {
                aPosition: pc.SEMANTIC_POSITION
            },
            vshader: vs,
            fshader: fs
        };

        return new pc.Shader(app.graphicsDevice, shaderDefinition);
    };

    HeightToNormal.copyShader = createShader(postEffectVert, copyFrag);
    HeightToNormal.blurShader = createShader(postEffectVert, blurFrag);
    HeightToNormal.normalMapShader = createShader(postEffectVert, normalMapFrag);
};
                 
HeightToNormal.prototype.createTexture = function (width, height) {
    return new pc.Texture(this.app.graphicsDevice, {
        width: width,
        height: height,
        format: pc.PIXELFORMAT_R8_G8_B8,
        minFilter: pc.FILTER_LINEAR,
        magFilter: pc.FILTER_LINEAR,
        addressU: pc.ADDRESS_CLAMP_TO_EDGE,
        addressV: pc.ADDRESS_CLAMP_TO_EDGE,
        mipmaps: false,
        anisotropy: 8
    });
};

HeightToNormal.prototype.createRenderTarget = function (texture) {
   return new pc.RenderTarget(this.app.graphicsDevice, texture, {
        depth: false
    });
};

HeightToNormal.prototype.createQuad = function () {
    var app = this.app;
    var vertexFormat = new pc.VertexFormat(app.graphicsDevice, [{
        semantic: pc.SEMANTIC_POSITION,
        components: 2,
        type: pc.ELEMENTTYPE_FLOAT32
    }]);
    var quad = new pc.VertexBuffer(app.graphicsDevice, vertexFormat, 4);
    var positions = new Float32Array(quad.lock());
    positions.set([-1, -1, 1, -1, -1, 1, 1, 1]);
    quad.unlock();

    return quad;
};

// Called once after all resources are loaded and before the first update
HeightToNormal.prototype.initialize = function () {
    if (!HeightToNormal.copyShader) {
        HeightToNormal.createShaders(this.app);
    }

    this.quad = this.createQuad();
};

HeightToNormal.prototype.generate = function (heightMap) {
    var app = this.app;
    var device = app.graphicsDevice;

    var w = heightMap.width;
    var h = heightMap.height;

    var colorBufferA = this.createTexture(w, h);
    var renderTargetA = this.createRenderTarget(colorBufferA);
    var colorBufferB = this.createTexture(w, h);
    var renderTargetB = this.createRenderTarget(colorBufferB);
    
    renderTargetA.colorBuffer.minFilter = pc.FILTER_LINEAR;
    renderTargetA.colorBuffer.mipmaps = false;

    // Render the height map into a render target (effectively a copy)
    device.setUniform("uColorBuffer", heightMap);
    pc.drawFullscreenQuad(device, renderTargetB, this.quad, HeightToNormal.copyShader);

    // Blur the height map
    for (var i = 0; i < this.blurIterations; i++) {
        device.setUniform("uColorBuffer", renderTargetB.colorBuffer);
        device.setUniform("uResolution", new Float32Array([heightMap.width, heightMap.height]));
        var radius = 0.2;
        device.setUniform("uDirection", new Float32Array(i % 2 === 0 ? [radius, 0] : [0, radius]));
        pc.drawFullscreenQuad(device, renderTargetA, this.quad, HeightToNormal.blurShader);

        var temp = renderTargetA;
        renderTargetA = renderTargetB;
        renderTargetB = temp;
    }

    renderTargetA.colorBuffer.minFilter = pc.FILTER_LINEAR_MIPMAP_LINEAR;
    renderTargetA.colorBuffer.mipmaps = true;

    // Generate the normal map from the height map
    device.setUniform("uHeightMap", renderTargetB.colorBuffer);
    device.setUniform("uResolution", new Float32Array([heightMap.width, heightMap.height]));
    device.setUniform("dz", this.strength * 100);
    device.setUniform("invertR", -1);
    device.setUniform("invertG", 1);
    device.setUniform("invertH", 1);
    device.setUniform("type", 0);
    pc.drawFullscreenQuad(device, renderTargetA, this.quad, HeightToNormal.normalMapShader);
    
    return {
        normalMap: renderTargetA.colorBuffer,
        heightMap: renderTargetB.colorBuffer
    };
};


