// tweenText.js
var TweenText = pc.createScript('tweenText');

// initialize code called once per entity
TweenText.prototype.initialize = function () {
    this.entity.tween(this.entity.getLocalScale())
        .to(new pc.Vec3(this.entity.getLocalScale().x * 1.15, this.entity.getLocalScale().y * 1.15, 1), .25, pc.CircularInOut,)
        .on('complete', function () {
        })
        .loop(true)
        .yoyo(true)
        .start();
};

// update code called every frame
TweenText.prototype.update = function (dt) {

};

