// skinDisplayer.js
var SkinDisplayer = pc.createScript('skinDisplayer');

// initialize code called once per entity
SkinDisplayer.prototype.initialize = function () {
    setTimeout(() => {
        this.entity.script.playerController.changeSpringStifness(10);

    }), 100;


    this.impulseRoutine()
};

SkinDisplayer.prototype.impulseRoutine = function (dt) {
    if (!this.entity.script) return;
    this.entity.script.playerController.head.rigidbody.applyImpulse(randomNumber(-.5, .5), 0, 0)

    setTimeout(() => {
        this.impulseRoutine();
    }, randomNumber(0, 15000));
};

// update code called every frame
SkinDisplayer.prototype.postUpdate = function (dt) {

};

