// shadeNormal.js
var ShadeNormal = pc.createScript('shadeNormal');
ShadeNormal.attributes.add('target', { type: 'entity' });

// initialize code called once per entity
ShadeNormal.prototype.initialize = function () {
    console.log("Material", this.entity)

    this.entity.sprite.material = this.target.sprite.material;
    this.entity.sprite.material.update()
    console.log("Modified", this.entity.sprite);
    console.log("Other", this.target.sprite.material);

};

// update code called every frame
ShadeNormal.prototype.update = function (dt) {

};

