// ElementScaling.js
var ElementScaling = pc.createScript('elementScaling');
ElementScaling.attributes.add('landScapeScaleMultiplier', { type: 'number', default: 1 })
ElementScaling.attributes.add('portraitScaleMultiplier', { type: 'number', default: 1 })

// initialize code called once per entity
ElementScaling.prototype.initialize = function () {
    this.baseScale = new pc.Vec3().copy(this.entity.getLocalScale());

    this.app.graphicsDevice.on('resizecanvas', (width, height) => {
        this.resize(width, height)
    });
};


ElementScaling.prototype.resize = function (width, height) {

    let ratio = (width / 2) / (height / 2);
    let constant = 0.9;
    let scale = 1 * ratio / constant;
    if (ratio > 1) {
        scale = this.landScapeScaleMultiplier * scale;
    }
    else {
        scale = this.portraitScaleMultiplier * ratio / constant;
    }
    this.entity.setLocalScale(this.baseScale.x * scale, this.baseScale.y * scale, scale)

};

// update code called every frame
ElementScaling.prototype.update = function (dt) {

};

