// rocket.js
var Rocket = pc.createScript('rocket');
Rocket.attributes.add('particles', {
    title: 'Particles',
    type: 'entity'
});
Rocket.attributes.add('power', {
    title: 'Power',
    type: 'number',
    default: 2
});
Rocket.attributes.add('maxPower', {
    title: 'MaxPower',
    type: 'number',
    default: 40
});
Rocket.attributes.add('otherCollider', {
    type: 'entity',
    default: 40
});
// initialize code called once per entity
Rocket.prototype.initialize = function () {
    this.rb = this.entity.rigidbody;


    this.activated = false;

    this.entity.collision.on('collisionstart', (event) => {
        this.activate(event);
    }, this);

    if (this.otherCollider) {
        this.otherCollider.collision.on('collisionstart', (event) => {
            this.activate(event);
        }, this);
    }




};



Rocket.prototype.activate = function (event) {
    if (this.activated || !event.other.tags._list.includes("Segment")) return;

    this.activated = true;
    this.particles.particlesystem.play()
};
// update code called every frame
Rocket.prototype.update = function (dt) {
    if (!this.activated) return;
    this.rb.linearVelocity = this.entity.up.mulScalar(this.power);
    if (this.power < this.maxPower) this.power *= 1.05;
};

