// rotationTween.js
var RotationTween = pc.createScript('rotationTween');

// initialize code called once per entity
RotationTween.prototype.initialize = function () {

/*     let baseScale = this.entity.getLocalScale();
    this.entity.setLocalScale(0, 0, 0);
    this.entity.tween(this.entity.getLocalScale())
        .to(new pc.Vec3(baseScale.x, baseScale.y, 1), .25, pc.CircularInOut,)
        .on('complete', function () {
        })

        .start() */;


    this.entity.tween(new pc.Vec3(this.entity.getLocalEulerAngles().x, this.entity.getLocalEulerAngles().y, -3))
        .rotate(new pc.Vec3(this.entity.getLocalEulerAngles().x, this.entity.getLocalEulerAngles().y, 3), 1, pc.CircularInOut,)
        .on('complete', function () {
        })
        .loop(true)
        .yoyo(true)
        .start();
};

// update code called every frame
RotationTween.prototype.update = function (dt) {
    let position = CameraManager.instance.entity.getPosition()
    this.entity.setPosition(position.x, this.entity.getPosition().y, 0)
};

