// GameManager.js

doStuffToEntity = function (asset, entity) {

    if (asset != undefined && asset.name != "Untitled") {
        var i = 0;
        for (i = 0; i < entity.children.length; ++i) {
            doStuffToEntity(asset.children[i], entity.children[i]);
        }
    }

    if (entity.hasOwnProperty("c") && entity.c.element != undefined && asset) {
        entity.setPosition(asset.getPosition());
    }
    if (entity.screen != undefined) {
        entity.c.screen.scaleBlend = asset.c.screen.scaleBlend;
    }
    if (entity.sprite != undefined && asset != undefined /* */) {
        if (asset.name == "Untitled") {
            asset = asset.parent.findByName(entity.name)
        }

        if (asset != undefined && asset.name != "Untitled" && asset.sprite) {
            entity.sprite.width = asset.sprite.width;
            entity.sprite.height = asset.sprite.height;
        }

    }
};

var GameManager = pc.createScript('gameManager');
GameManager.attributes.add('camera', { type: 'entity' });
GameManager.attributes.add('menu', { type: 'string' });
GameManager.attributes.add('shop', { type: 'string' });
GameManager.attributes.add('sausageTemplate', { type: 'asset', assetType: 'template' });
GameManager.attributes.add('winParticles', { type: 'entity' });
GameManager.attributes.add('winText', { type: 'entity' });
GameManager.attributes.add('test', { type: 'boolean', default: false });
GameManager.attributes.add('skinList', {
    type: 'asset',
    assetType: 'json'
});

// initialize code called once per entity
GameManager.prototype.initialize = function () {

    GameManager.instance = this;
    this.app.systems.rigidbody.fixedTimeStep = 1 / 120;
    console.log = () => { };
    this.levelId = SaveManager.instance.getItem("LevelId");
    this.levelStarted = false;
    this.finished = false;
    this.respawned = false;

    /*   setTimeout(() => {
          var dummyEl = document.getElementById("");
  
          // check for focus
          var isFocused = (document.activeElement === dummyEl);
          console.log("isFocused ", isFocused)
      }, 2000);
   */


};

GameManager.prototype.postInitialize = function () {
    if (!this.test && !SaveManager.instance.showTutorial) {
        this.mainMenu();
    }
    else if (SaveManager.instance.showTutorial) {
        this.startLevel();
    }
    setTimeout(() => {
        //   this.unlockLevelSkin("Unicorn")

    })

    //this.shopScene()


}


GameManager.prototype.spawnCharacter = function () {
    let instance = this.app.assets.find(SaveManager.instance.getItem("CurrentSkin"), "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find(SaveManager.instance.getItem("CurrentSkin"), "template").resource._templateRoot, instance)
    instance.setPosition(this.level.findByName("SpawnPoint").getPosition());
    instance.script.playerController.enabled = true;
    this.app.root.addChild(instance);
    this.playerController = instance.script.playerController;

};

GameManager.prototype.mainMenu = function () {
    PokiSDK.gameplayStop();
    PokiManager.instance.displayBanner();
    this.levelStarted = false;
    SkinsButton.instance.showButton();


    let menu = this.app.assets.find("Main Menu", "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find("Main Menu", "template").resource._templateRoot, menu);
    this.app.root.addChild(menu);
    menu.setPosition(new pc.Vec3(0, 18, 0));
    menu.findByName("Logo").findByName("LevelText").element.text = "CHAPTER " + this.levelId + '/' + Math.ceil(this.levelId / 10) * 10

    this.start = this.app.assets.find("Pattern_Start", "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find("Pattern_Start", "template").resource._templateRoot, this.start);
    this.start.setPosition(-17.198, -2.65 - menu.getPosition().y, 0)

    menu.addChild(this.start);

    this.app.timeScale = 1;



    if (this.level)
        this.level.destroy();
    this.level = menu;
    if (this.playerController) {
        this.playerController.entity.destroy();
    }
    this.spawnCharacter()
    this.playerController.activated = false;

    return menu;
};

GameManager.prototype.transitionScene = function () {
    if (this.level)
        this.level.destroy();
    this.level.name = "Main Menu";

    if (this.playerController) {
        this.playerController.entity.destroy();
    }

    let instance = this.app.assets.find("Transition", "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find("Transition", "template").resource._templateRoot, instance);
    this.app.root.addChild(instance);

};

GameManager.prototype.shopScene = function () {
    this.levelStarted = false;
    this.app.timeScale = 1;
    PokiSDK.gameplayStop();
    PokiManager.instance.displayBanner();
    SkinsButton.instance.hideButton(.1)

    if (this.playerController) {
        this.playerController.entity.destroy();
    }


    let instance = this.app.assets.find("Shop", "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find("Shop", "template").resource._templateRoot, instance);
    this.app.root.addChild(instance);
    this.lastScreen = this.level;


    if (this.level)
        this.level.destroy();
    this.level = instance;

    return instance;
};


GameManager.prototype.unlockLevelSkin = function (skin) {


    let instance = this.app.assets.find("New Skin", "template").resource.instantiate();
    doStuffToEntity(this.app.assets.find("New Skin", "template").resource._templateRoot, instance);

    if (skin) {
        instance.script.newSkinPopup.skin = skin.name;
        let skinList = SaveManager.instance.getItem("Skins")
        skinList.push(skin.name)
        SaveManager.instance.setItem("Skins", skinList);
    }

    else {

        let pool = Object.values(this.skinList.resources[0]).filter(skin => skin.type == "REWARDED" && !SaveManager.instance.getItem("Skins").includes(skin.name))
        if (pool.length == 0) return;
        let rand = Math.round(Math.random() * ((pool.length - 1) - 0) + 0)
        instance.script.newSkinPopup.rewarded = true;
        instance.script.newSkinPopup.skin = pool[rand].name;

    }
    this.app.root.addChild(instance);

};





GameManager.prototype.startLevel = function (respawned) {
    PokiSDK.gameplayStart();

    PokiSDK.customEvent('game', 'segment', { label: 'level', value: this.levelId });
    SkinsButton.instance.showButton();

    this.app.timeScale = 1;




    setTimeout(() => {

        SaveManager.instance.setItem("LevelId", this.levelId);
        let unlockedChar = false
        if (!respawned)
            unlockedChar = this.checkSkinUnlock();

        if (this.level)
            this.level.destroy();
        this.level = LevelGenerator.instance.spawnLevel(this.levelId);
        this.levelStarted = true;



        if (this.playerController) {
            this.playerController.entity.destroy();
        }
        this.spawnCharacter()
        this.playerController.activated = !unlockedChar;


        this.finished = false;
    }, 1);


};

GameManager.prototype.finishedLevel = function () {
    PokiManager.instance.displayBanner();

    PokiSDK.gameplayStop();
    PauseButton.instance.hide()
    this.finished = true;
    this.respawned = false
    this.levelStarted = false;

    this.app.timeScale = 1;
    SkipLevel.instance.hideButton();
    let timeScale = { x: 1 };
    CameraManager.instance.flash(2)
    this.winParticles.children.forEach((child) => {
        child.enabled = true;
    })
    setTimeout(() => {
        this.entity
            .tween(timeScale)
            .to({ x: 0.1 }, .3, pc.Linear)
            .on('update', () => {
                this.app.timeScale = timeScale.x;
                this.winParticles.children.forEach((child) => {
                    child.particlesystem.emitter.fixedTimeStep = 0.016 * this.app.timeScale
                });
            })
            .start();

        this.winText.enabled = true;
        this.winText.findByName("MaskRound").script.maskTransform.test()

        this.winText
            .tween(new pc.Vec3(0, 0, 180))
            .rotate(new pc.Vec3(0, 0, 0), .5, pc.Linear)
            .on('complete', () => {
                this.winText
                    .tween(new pc.Vec3(0, 0, 0))
                    .rotate(new pc.Vec3(0, 0, -10), .5, pc.Linear)
                    .on('complete', () => {
                    })
                    .start();
            })
            .start();

    }, 200);


    let orthoHeight = { x: this.camera.camera.orthoHeight };
    this.entity
        .tween(orthoHeight)
        .to({ x: orthoHeight.x / 1.5 }, .5, pc.Linear)
        .on('update', () => {
            this.camera.camera.orthoHeight = orthoHeight.x;
        })
        .start();


    this.winParticles.children.forEach((child) => {

        child.particlesystem.reset();
        child.particlesystem.play();

    });

    setTimeout(() => {
        PokiManager.instance.interstitial(() => {
            this.winParticles.children.forEach((child) => {
                child.enabled = false;
            });
            let orthoHeight = { x: this.camera.camera.orthoHeight };

            this.entity
                .tween(orthoHeight)
                .to({ x: 25 }, .3, pc.Linear)
                .on('update', () => {
                    this.camera.camera.orthoHeight = orthoHeight.x;
                })
                .start();
            this.levelId++;
            this.winText.enabled = false;

            this.transitionScene()
        });





    }, 2000);



};

GameManager.prototype.checkSkinUnlock = function (dt) {

    let skinUnlock = Object.values(this.skinList.resources[0]).find(skin => skin.level == (this.levelId))

    if (skinUnlock && !SaveManager.instance.getItem("Skins").includes(skinUnlock.name)) {
        this.unlockLevelSkin(skinUnlock);
        return true;
    }
    else if (Math.round(Math.random() * (5 - 0) + 0) == 1 && this.levelId > 10) {
        this.unlockLevelSkin();
        return true;
    }

    else {
        //this.startLevel()
        SkinsButton.instance.showButton();
        return false;
    }
};

// update code called every frame
GameManager.prototype.postUpdate = function (dt) {
    window.compound = this.app.systems.rigidbody._compounds;
    window.dynamic = this.app.systems.rigidbody._dynamic;
    window.kinematic = this.app.systems.rigidbody._kinematic;
    //  console.log(this.app.systems.rigidbody)
    if (this.levelStartFlag) {
        this.levelStartFlag = false;
        this.startLevel()
    }
};

