// testPrefab.js
var TestPrefab = pc.createScript('testPrefab');
TestPrefab.attributes.add('toClone', { type: 'entity' });

// initialize code called once per entity
TestPrefab.prototype.initialize = function () {
    let clone = this.toClone.clone(true);
    let position = clone.getPosition();
    clone.setPosition(position.x + 5, position.y, position.z)
    this.app.root.addChild(clone)
    /*     console.log("Template", this.toClone.c.sprite)
        console.log("Clone", clone.c.sprite)
     */
};

// update code called every frame
TestPrefab.prototype.update = function (dt) {

};

