// setGravity.js
var SetGravity = pc.createScript('setGravity');

SetGravity.attributes.add('gravityScaleY', { type: 'number' })
SetGravity.attributes.add('baseGravityY', { type: 'number' })

// initialize code called once per entity
SetGravity.prototype.postInitialize = function () {
    let gravity = this.app.systems.rigidbody.dynamicsWorld.getGravity();
    setTimeout(() => {
        this.entity.rigidbody.body.setGravity(new Ammo.btVector3(0, this.baseGravityY * this.gravityScaleY, 0))
        this.entity.rigidbody.body.setLinearVelocity(new Ammo.btVector3(0, 0, 0))
    }, 100)


};


