// setSpriteColor.js
var SetSpriteColor = pc.createScript('setSpriteColor');

// initialize code called once per entity
SetSpriteColor.prototype.initialize = function () {
    if (this.entity.sprite) {
        if (this.entity.tags._list.includes("NotSticky")) {
            this.entity.sprite.color = ThemeManager.instance.colorBouncyGpeColor;
        }
        else {
            this.entity.sprite.color = ThemeManager.instance.colorStickyGpeColor;
        }
    }
};

// update code called every frame
SetSpriteColor.prototype.update = function (dt) {

};

