// springTest.js
var SpringTest = pc.createScript('springTest');

// initialize code called once per entity
SpringTest.prototype.postInitialize = function () {
    this.done = false;
};

// update code called every frame
SpringTest.prototype.postUpdate = function (dt) {
    if (!this.done) {
        this.done = true;

        this.entity.script.springJoint.enabled = true;
        this.entity.script.springJoint.createConstraint();
    }
};

