// saveManager.js
var SaveManager = pc.createScript('saveManager');

// initialize code called once per entity
// initialize code called once per entity
SaveManager.prototype.initialize = async function () {

    SaveManager.instance = this;


    if (this.getItem("FirstConncection") === null) {
        this.setItem("FirstConncection", true);
        // this.firstConnection = true;
        this.showTutorial = true;
        this.setItem("LevelId", 0);
        //this.levelId = 0;
        this.setItem("lastLevelId", -1);
        //this.lastLevelId = -1;
        this.setItem("currentLevelDeck", []);
        //this.currentLevelDeck = [];
        this.setItem("Skins", ['Basic']);
        //this.skins = ['Basic']
        this.setItem("CurrentSkin", 'Basic');
        //this.currentSkin = 'Basic'
    }

};



SaveManager.prototype.getItem = function (name) {

    try {
        return JSON.parse(localStorage.getItem(name));
    } catch (err) {
        console.error(err);
        if (name == "First Connection") {
            return this.firstConnection;
        }
        else if (name == "LevelId") {
            return this.levelId;
        }
        else if (name == "lastLevelId") {
            return this.lastLevelId;
        }
        else if (name == "currentLevelDeck") {
            return this.currentLevelDeck;
        }
        else if (name == "Skins") {
            return this.skins;
        }
        else if (name == "CurrentSkin") {
            return this.currentSkin;
        }

        return null;
    }
}

SaveManager.prototype.setItem = function (name, value) {
    try {
        localStorage.setItem(name, JSON.stringify(value));
    } catch (err) {
        console.error(err);

        if (name == "First Connection") {
            this.firstConnection = value;
        }
        else if (name == "LevelId") {
            this.levelId = value;
        }
        else if (name == "lastLevelId") {
            this.lastLevelId = value;
        }
        else if (name == "currentLevelDeck") {
            this.currentLevelDeck = value;
        }
        else if (name == "Skins") {
            this.skins = value;
        }
        else if (name == "CurrentSkin") {
            this.currentSkin = value;
        }
    }
}


