// disableAd.js
var DisableAd = pc.createScript('disableAd');

// initialize code called once per entity
DisableAd.prototype.initialize = function () {
    this.app.on('shopAdblockPopup', () => {
        this.showPopup()
    })
};


DisableAd.prototype.showPopup = function () {
    let alpha = { x: 0 };

    let tween = this.entity.tween(alpha);
    tween.to({ x: 1 }, 1, pc.ExponentialOut, 0, 1)
        .on('update', () => {
            tween.manager.setTs(1 / this.app.timeScale)
            this.entity.element.opacity = alpha.x;
        })
        .yoyo(true)
        .repeat(2)
        .start();
};

// update code called every frame
DisableAd.prototype.update = function (dt) {

};

