// progressBar.js
var ProgressBar = pc.createScript('progressBar');
ProgressBar.attributes.add('curLevel', { type: 'entity' });
ProgressBar.attributes.add('nextLevel', { type: 'entity' });

// initialize code called once per entity
ProgressBar.prototype.initialize = function () {
    this.percentage = 0;
    this.curLevel.element.text = GameManager.instance.levelId;
    this.nextLevel.element.text = GameManager.instance.levelId + 1;
};

// update code called every frame
ProgressBar.prototype.update = function (dt) {
    this.entity.element.width = Math.min(Math.max(GameManager.instance.playerController.sausageSegments[0].getPosition().x * 9.288 / GameManager.instance.level.findByName("Pattern_Finish").getPosition().x, 0), 9.288)

};

