// MaskTransform.js
var MaskTransform = pc.createScript('maskTransform');
MaskTransform.attributes.add('baseText', { type: 'entity' });

// initialize code called once per entity
MaskTransform.prototype.initialize = function () {

    this.initPos = new pc.Vec3().copy(this.entity.getLocalPosition());
    this.initPosChild = new pc.Vec3().copy(this.entity.children[0].getLocalPosition());


};



// update code called every frame
MaskTransform.prototype.test = function (dt) {

    this.entity.setLocalPosition(this.initPos.x, this.initPos.y, this.initPos.z);
    this.entity.children[0].setLocalPosition(this.initPosChild.x, this.initPosChild.y, this.initPosChild.z);

    this.entity
        .tween(this.entity.getLocalPosition())
        .to(new pc.Vec3(this.entity.getLocalPosition().x + 90, this.entity.getLocalPosition().y, this.entity.getLocalPosition().z), 2, pc.Linear)
        .on('update', () => {
            this.entity.children[0].setPosition(this.baseText.getPosition().x, this.baseText.getPosition().y, this.baseText.getPosition().z);
        })
        .on('complete', () => {

        })
        .start();
    /* let child = this.entity.children[0];
    let parent2 = this.entity.parent;
    var mat = child.getWorldTransform().clone();
    var parentMat = parent2.getWorldTransform().clone().invert();
    mat.mul2(parentMat, mat);
    var pos = mat.getTranslation();
    var rot = new pc.Quat().setFromMat4(mat);
    var scale = mat.getScale();

    child.reparent(parent2);

    child.setLocalPosition(pos);
    child.setLocalRotation(rot);
    child.setLocalScale(scale); */
};

