// springJointPivot.js
var SpringJointPivot = pc.createScript('springJointPivot');

// initialize code called once per entity
SpringJointPivot.prototype.initialize = function () {
    this.spring = this.entity.script.springJoint;
    console.log()
    this.connectedTo = this.spring.entityB;
    this.spring.pivotA = this.entity.getPosition().sub(this.connectedTo.getPosition()).mulScalar(-1);
    this.spring.createConstraint()
    console.log("Offset", this.entity.getPosition().sub(this.connectedTo.getPosition()).mulScalar(-1))
};

// update code called every frame
SpringJointPivot.prototype.update = function (dt) {

};

