// skinsButton.js
var SkinsButton = pc.createScript('skinsButton');
SkinsButton.attributes.add('skinsButton', { type: 'entity' })

// initialize code called once per entity
SkinsButton.prototype.initialize = function () {
    SkinsButton.instance = this;
    this.hidden = true;

    // this.skinsButton = this.entity.children[0];
};

// update code called every frame
SkinsButton.prototype.showButton = function (dt) {

    if (!this.skinsButton.enabled) {

        this.skinsButton.enabled = true;

        let tween = this.skinsButton.tween(this.skinsButton.getLocalPosition());
        tween.to(new pc.Vec3(30, this.skinsButton.getLocalPosition().y, 0), .5, pc.ElasticOut)
            .on('complete', () => {

            })
            .on('update', () => {
                tween.manager.setTs(1 / this.app.timeScale);
            })
            .start();

    }

};

SkinsButton.prototype.hideButton = function (time) {

    if (!time) {
        time = .5
    }

    if (this.skinsButton.enabled) {
        this.hidden = true;

        let tween = this.skinsButton.tween(this.skinsButton.getLocalPosition());

        tween.to(new pc.Vec3(140, this.skinsButton.getLocalPosition().y, 0), time, pc.ElasticIn)
            .on('complete', () => {
                this.skinsButton.enabled = false;
            })
            .on('update', () => {
                tween.manager.setTs(1 / this.app.timeScale);
            })
            .start();
    }

};



