// Parallax.js
var Parallax = pc.createScript('parallax');
Parallax.attributes.add('horizontalSpeed', { type: 'number' });
Parallax.attributes.add('verticalSpeed', { type: 'number' });

// initialize code called once per entity
Parallax.prototype.initialize = function () {

};

// update code called every frame
Parallax.prototype.update = function (dt) {
    if (CameraManager.instance.enabled) {

        let camPos = CameraManager.instance.entity.getPosition();
        let pos = this.entity.getPosition();

        this.entity.setPosition(-camPos.x * this.horizontalSpeed, -camPos.y - this.verticalSpeed, 0);
    }
};

