// skinBlister.js
var SkinBlister = pc.createScript('skinBlister');
SkinBlister.attributes.add('toColorize', {
    type: 'entity',
    array: true
});
SkinBlister.attributes.add('type', { type: 'string' });
SkinBlister.attributes.add('levelText', { type: 'entity' });
SkinBlister.attributes.add('skinSpawnPoint', { type: 'entity' });

// initialize code called once per entity
SkinBlister.prototype.initialize = function () {
    this.unlocked = false;
    // this.selected = false;
    this.skinName = "";
    this.entity.rigidbody.enabled = true;
    this.createConstraint = true;



};
SkinBlister.prototype.postInitialize = function () {


}

SkinBlister.prototype.setSelected = function (selected) {
    this.selected = selected;
    this.entity.findByName("Selected").enabled = selected;
    if (selected) {
        SaveManager.instance.setItem("CurrentSkin", this.skin);
        ShopManager.instance.selectedSkin = this;
        ShopManager.instance.displaySkin();
        let alpha = { x: .3 };

        this.tween = this.entity.findByName("Selected").children[0].tween(alpha);
        this.tween.to({ x: 1 }, .6, pc.Linear)
            .on('update', () => {
                this.entity.findByName("Selected").children[0].sprite.opacity = alpha.x;
            })
            .yoyo(true)
            .loop(true)
            .start();

        let alpha2 = { x: 0 };

        this.tween2 = this.entity.findByName("Selected").children[1].tween(alpha2);
        this.tween2.to({ x: .25 }, .6, pc.Linear)
            .on('update', () => {
                this.entity.findByName("Selected").children[1].sprite.opacity = alpha2.x;
            })
            .yoyo(true)
            .loop(true)
            .start();
    }
    else if (this.tween && this.tween2) {
        this.tween2.stop()
        this.tween.stop()
        this.tween2 = null;
        this.tween = null;
    }



}

SkinBlister.prototype.setRewarded = function () {
    this.entity.findByName("Button").button.off('click')
    this.entity.findByName("Button").button.on('click', () => {
        if (ShopManager.instance.axis == 2) {
            PokiManager.instance.rewarded((result) => {
                console.log(result)
                if (result) {
                    ShopManager.instance.unlockSkin(this)
                    GameManager.startLevel()
                }
            }, { category: "cosmetic", detail: this.skin, placement: "gameplay" })
        }
    });
    this.entity.findByName("Unlocked").enabled = false;
    this.entity.findByName("LockedRewarded").enabled = true;
    this.entity.findByName("Locked").enabled = false;
}
SkinBlister.prototype.setBasic = function () {
    this.entity.findByName("Button").button.off('click')
    this.entity.findByName("Button").button.on('click', () => {
        console.log("Level")
    });
    this.entity.findByName("Unlocked").enabled = false;
    this.entity.findByName("LockedRewarded").enabled = false;
    this.entity.findByName("Locked").enabled = true;
}



SkinBlister.prototype.unlock = function () {
    this.entity.findByName("Button").button.off('click')
    this.entity.findByName("Button").button.on('click', () => {
        if (ShopManager.instance.axis == 2) {
            if (this.selected) {
                GameManager.instance.startLevel()
            }
            else {
                ShopManager.instance.selectedSkin.setSelected(false);
                this.setSelected(true);
            }
        }
    });
    this.entity.findByName("Unlocked").enabled = true;
    this.entity.findByName("LockedRewarded").enabled = false;
    this.entity.findByName("Locked").enabled = false;
    this.unlocked = true;

};

// update code called every frame
SkinBlister.prototype.postUpdate = function (dt) {
    if (this.createConstraint) {
        this.createConstraint = false;
        this.entity.script.hingeConstraint.enabled = true;

        this.entity.script.hingeConstraint.createConstraint();

    }

};

