// trigger-layer.js
var TriggerLayer = pc.createScript('triggerLayer');

TriggerLayer.attributes.add('groupA', {type: 'boolean', default: false, title: 'Group A'});
TriggerLayer.attributes.add('groupB', {type: 'boolean', default: false, title: 'Group B'});
TriggerLayer.attributes.add('groupC', {type: 'boolean', default: false, title: 'Group C'});
TriggerLayer.attributes.add('groupD', {type: 'boolean', default: false, title: 'Group D'});

// initialize code called once per entity
TriggerLayer.prototype.initialize = function() {
    var body = this.entity.trigger.body;
    var flags = 4;
    
    // Groups
    if (this.groupA) {
        flags |= (pc.BODYGROUP_USER_1);
    }

    if (this.groupB) {
        flags |= (pc.BODYGROUP_USER_2);
    }

    if (this.groupC) {
        flags |= (pc.BODYGROUP_USER_3);
    }

    if (this.groupD) {
       flags |= (pc.BODYGROUP_USER_4);
    }
    
    body.setCollisionFlags(flags);
};

