// themeManager.js
var ThemeManager = pc.createScript('themeManager');
ThemeManager.attributes.add('themes', { type: 'asset', assetType: 'json' });
ThemeManager.attributes.add('pattern', { type: 'entity' });
ThemeManager.attributes.add('background', { type: 'entity' });
ThemeManager.attributes.add('gradient1', { type: 'entity' });
ThemeManager.attributes.add('gradient2', { type: 'entity' });

// initialize code called once per entity
ThemeManager.prototype.initialize = function () {
    ThemeManager.instance = this;
    this.changeTheme()

};
ThemeManager.prototype.changeTheme = function () {
    let themeIndex = Math.floor(GameManager.instance.levelId / 10 % Object.values(this.themes.resource).length);
    this.theme = Object.values(this.themes.resource)[themeIndex];
    GameManager.instance.camera.camera.clearColor = new pc.Color().fromString(this.theme.backgroundColor);
    this.gradient1.sprite.color = new pc.Color().fromString(this.theme.backgroundElementsColor);
    this.gradient2.sprite.color = this.gradient1.sprite.color;
    this.pattern.sprite.spriteAsset = this.app.assets.find(this.theme.backgroundPattern, "sprite");
    this.pattern.sprite.color = this.gradient1.sprite.color;
    this.colorStickyGpeColor = new pc.Color().fromString(this.theme.stickyGpeColor);
    this.colorBouncyGpeColor = new pc.Color().fromString(this.theme.bouncyGpeColor);

};


