// RotateCylinder.js
var RotateCylinder = pc.createScript('rotateCylinder');

// initialize code called once per entity
RotateCylinder.prototype.initialize = function() {
    this.x = pc.math.random(0, 90);
    this.y = pc.math.random(0, 90);
    this.z = pc.math.random(0, 90);
};

// update code called every frame
RotateCylinder.prototype.update = function(dt) {
    this.entity.rotate(this.x * dt, this.y * dt, this.z * dt);
};

