// spring.js

var SpringJoint = pc.createScript('springJoint');

SpringJoint.attributes.add('entityA', {
    title: 'entityA',
    description: 'Optional second connected entity.',
    type: 'entity'
});

SpringJoint.attributes.add('pivotA', {
    title: 'pivotA',
    description: 'Position of the constraint in the local space of this entity.',
    type: 'vec3',
    default: [0, 0, 0]
});
SpringJoint.attributes.add('axisA', {
    title: 'axisA',
    description: 'Axis of rotation of the constraint in the local space this entity.',
    type: 'vec3',
    default: [0, 1, 0]
});
SpringJoint.attributes.add('entityB', {
    title: 'entityB',
    description: 'Optional second connected entity.',
    type: 'entity'
});
SpringJoint.attributes.add('pivotB', {
    title: 'pivotB',
    description: 'Position of the constraint in the local space of the connected entity (if specified).',
    type: 'vec3',
    default: [0, 0, 0]
});
SpringJoint.attributes.add('axisB', {
    title: 'axisB',
    description: 'Axis of rotation of the constraint in the local space of the connected entity (if specified).',
    type: 'vec3',
    default: [0, 1, 0]
});
SpringJoint.attributes.add('stifness', {
    title: 'stifness',
    description: 'Linear limits of the constraint.',
    type: 'number',
    default: 10
});
SpringJoint.attributes.add('damping', {
    title: 'damping',
    description: 'Angular limits of the constraint.',
    type: 'number',
    default: 0.1
});

SpringJoint.attributes.add('enableCollision', {
    title: 'Enable Collision',
    description: 'Enable collision between linked rigid bodies.',
    type: 'boolean',
    default: true
});
SpringJoint.attributes.add('debugRender', {
    title: 'Debug Render',
    description: 'Enable to render a representation of the constraint.',
    type: 'boolean',
    default: false
});
SpringJoint.attributes.add('debugColor', {
    title: 'Debug Color',
    description: 'The color of the debug rendering of the constraint.',
    type: 'rgb',
    default: [1, 0, 0]
});

// initialize code called once per entity
SpringJoint.prototype.initialize = function () {
    this.createConstraint();

    this.on('attr', function (name, value, prev) {

    });
    this.on('enable', function () {
        this.createConstraint();
    });
    this.on('disable', function () {
        this.destroyConstraint();
    });
    this.on('destroy', function () {
        this.destroyConstraint();
    });
};

SpringJoint.prototype.createConstraint = function () {
    if (this.constraint) {
        this.destroyConstraint();
    }

    let axisA = new Ammo.btVector3(this.axisA.x, this.axisA.y, this.axisA.z)
    let axisB = new Ammo.btVector3(this.axisB.x, this.axisB.y, this.axisB.z)
    let pivotA = new Ammo.btVector3(this.pivotA.x, this.pivotA.y, this.pivotA.z);
    let pivotB = new Ammo.btVector3(this.pivotB.x, this.pivotB.y, this.pivotB.z);

    let _tmpAmmoQuaternion = new Ammo.btQuaternion(0, 0, 0, 1);
    //Change this to the length of the spring?
    /*     let _tmpAmmoVectorA = new Ammo.btVector3(0, 0, 0); //seems to be expressed in local world
        let _tmpAmmoVectorB = new Ammo.btVector3(0, 0, 0); */

    //Is it a correct thing to create btTransform ? Is it not bind to a rigidbody ?
    let transformA = new Ammo.btTransform(_tmpAmmoQuaternion, pivotA);
    let transformB = new Ammo.btTransform(_tmpAmmoQuaternion, pivotB);


    /*Ammo joint: 
        ammo rigidbody = physicsImpostor.physicsBody 
        We also need a reference to the world:scene.physicsPlugin.world
        For a point2point joint, a pivot is need, it is a simple btVector3
        But for a spring constraint, a btTransform is requested !
    */
    this.constraint =
        new Ammo.btGeneric6DofSpringConstraint(
            this.entityA.rigidbody.body,
            this.entityB.rigidbody.body,
            transformA,
            transformB,
            true
        );

    this.constraint.setLinearLowerLimit(axisA);
    this.constraint.setLinearUpperLimit(new Ammo.btVector3(0, 0, 0));

    //Here the indexes are refering to the axes. 1 is for y
    this.constraint.enableSpring(1, true);
    this.constraint.setStiffness(1, this.stifness);
    this.constraint.setDamping(1, this.damping);
    this.app.systems.rigidbody.dynamicsWorld.addConstraint(this.constraint, !this.enableCollision); //adding the new constraint to the physics world
    //this.activate();
};

SpringJoint.prototype.destroyConstraint = function () {
    if (this.constraint) {

        var dynamicsWorld = this.app.systems.rigidbody.dynamicsWorld;
        dynamicsWorld.removeConstraint(this.constraint);
        Ammo.destroy(this.constraint);
        this.constraint = null;
    }
};

SpringJoint.prototype.activate = function () {
    this.entity.rigidbody.activate();
    if (this.entityB) {
        this.entityB.rigidbody.activate();
    }
};

// update code called every frame
SpringJoint.prototype.update = function (dt) {
    if (this.debugRender) {
        // Note that it's generally bad to allocate new objects in an update function
        // but this is just for debug rendering and will normally be disabled
        var tempVecA = new pc.Vec3();
        this.entityA.getWorldTransform().transformPoint(this.pivotA, tempVecA);
        var tempVecB = new pc.Vec3();
        this.entityB.getWorldTransform().transformPoint(this.pivotB, tempVecB);
        this.app.renderLine(tempVecA, tempVecB, this.debugColor);
        if (this.entityB) {
            /*             var tempVecB = new pc.Vec3();
                        this.entity.getWorldTransform().transformPoint(this.pivotB, tempVecA);
                        this.app.renderLine(this.entityB.getPosition(), tempVecB, this.debugColor); */
        }
    }
}


