// LevelGenerator.js
var LevelGenerator = pc.createScript('levelGenerator');

LevelGenerator.attributes.add('uiTemplate', { type: 'asset', assetType: 'template' });
LevelGenerator.attributes.add('sausageTemplate', { type: 'asset', assetType: 'template' });
LevelGenerator.attributes.add('levelData', {
    type: 'asset',
    assetType: 'json'
});

LevelGenerator.attributes.add('decks', {
    type: 'asset',
    assetType: 'json'
});
// initialize code called once per entity
LevelGenerator.prototype.initialize = function () {
    LevelGenerator.instance = this;
};


LevelGenerator.prototype.spawnLevel = function (levelId) {

    let currentLevelDeck;

    if (SaveManager.instance.getItem("lastLevelId") == levelId) {
        currentLevelDeck = SaveManager.instance.getItem("currentLevelDeck");
    }

    else {
        SaveManager.instance.setItem("lastLevelId", levelId);

        if (Object.values(this.levelData.resources[0]).length > GameManager.instance.levelId) {
            if (Object.values(this.levelData.resources[0])[GameManager.instance.levelId] != null) {
                currentLevelDeck = Object.values(this.levelData.resources[0])[GameManager.instance.levelId];
            }
            else {
                currentLevelDeck = this.generateLevel(GameManager.instance.levelId);
            }
        }
        else {
            currentLevelDeck = this.generateLevel(GameManager.instance.levelId);
        }


        SaveManager.instance.setItem("currentLevelDeck", currentLevelDeck);
    }
    return this.instantiateLevel(currentLevelDeck);


};
LevelGenerator.prototype.instantiateLevel = function (levelData) {
    var level = new pc.Entity({ name: "Level" });

    this.app.root.addChild(level);
    level.localPosition = new pc.Vec3(0, 0, 0);
    level.setPosition(new pc.Vec3(0, 0, 0));

    let patterns = levelData.patterns;
    if (patterns[0] != "Pattern_Start_Onboarding") {
        start = this.app.assets.find("Pattern_Start", "template").resource.instantiate();
        doStuffToEntity(this.app.assets.find("Pattern_Start", "template").resource._templateRoot, start);
        start.setPosition(-17.198, -2.65, 0)
        level.addChild(start);
        this.position = start.findByName("End").getPosition();

    }

    else {
        this.position = new pc.Vec3(-1.259, 15.747, 0);
    }

    patterns.forEach((pattern) => {
        console.log(pattern)
        let asset = this.app.assets.find(pattern, "template", pattern).resource
        let instance = this.app.assets.find(pattern, "template", pattern).resource.instantiate();
        instance.localPosition = new pc.Vec3(0, 0, 0)
        instance.setPosition(this.position);
        doStuffToEntity(asset._templateRoot, instance);
        this.position = instance.findByName("End").getPosition();
        level.addChild(instance);

    });



    let asset = this.uiTemplate.resource;
    let ui = this.uiTemplate.resource.instantiate();
    doStuffToEntity(asset._templateRoot, ui);
    level.addChild(ui);
    ThemeManager.instance.changeTheme();

    return level;

}
LevelGenerator.prototype.generateLevel = function (levelId) {
    let level = { patterns: [] };

    let nbPatterns = Math.floor(Math.random() * (1 + 6 - 3)) + 3;
    for (let index = 0; index < nbPatterns; index++) {
        level.patterns.push(this.pickRandomPattern(levelId));
    }
    level.patterns.push("Pattern_End");

    return level;
};

LevelGenerator.prototype.pickRandomPattern = function (levelId) {
    let total = 0;
    let availableDecks = [];
    Object.values(this.decks.resources[0]).forEach((deck) => {
        if (deck == null) return;
        if (levelId < deck.unlockLevel) return;
        total += deck.weight;
        availableDecks.push(deck);
    });

    let rand = Math.floor(Math.random() * (1 + total - 0)) + 0;
    let index = -1;

    while (rand > 0 && index < availableDecks.length) {
        index++;
        rand -= availableDecks[index].weight;
    }

    index = Math.min(Math.max(index, 0), availableDecks.length);
    let chosenDeck = availableDecks[index];
    if (chosenDeck.index >= chosenDeck.patterns.length) chosenDeck.index = 0;
    let chosenPattern = chosenDeck.patterns[chosenDeck.index++];
    return chosenPattern;

};


// update code called every frame
LevelGenerator.prototype.update = function (dt) {

};

