// skipLevel.js
var SkipLevel = pc.createScript('skipLevel');
SkipLevel.attributes.add('nextLevel', { type: 'entity' });

// initialize code called once per entity
SkipLevel.prototype.initialize = function () {
    SkipLevel.instance = this;

    if (GameManager.instance.respawned) {
        this.showButton()
    }
}

// update code called every frame
SkipLevel.prototype.showButton = function (dt) {


    this.nextLevel.enabled = true;

    let tween = this.nextLevel.tween(this.nextLevel.getLocalPosition());
    tween.to(new pc.Vec3(10, this.nextLevel.getLocalPosition().y, 0), .5, pc.ElasticOut)
        .on('complete', () => {
        })
        .on('update', () => {
            tween.manager.setTs(1 / this.app.timeScale);
        })
        .start();

}


SkipLevel.prototype.hideButton = function (time) {

    if (!time) {
        time = .5
    }


    let tween = this.nextLevel.tween(this.nextLevel.getLocalPosition());

    tween.to(new pc.Vec3(80, this.nextLevel.getLocalPosition().y, 0), time, pc.ElasticIn)
        .on('complete', () => {
            this.nextLevel.enabled = false;
        })
        .on('update', () => {
            tween.manager.setTs(1 / this.app.timeScale);
        })
        .start();


};

